import React from "react";
import Programmer from "../images/htmlcss.svg";

const Title = () => {
  return (
    <div>
      <div className="container-fluid">
        <div className="jumbotron text-center py-5">
          <img
            src={Programmer}
            alt="Logo"
            style={{ width: "25px", display: "inline-block" }}
          />
          <h1 style={{ display: "inline-block" }}>Programmer's Republic</h1>
        </div>
      </div>
    </div>
  );
};

export default Title;
